<template>
  <div v-if="loading" class="">
    <LoadSkeletonPlay></LoadSkeletonPlay>
  </div>
  <div v-else class="content">
    <div class="watch-player-content flex gap-5 justify-between">
      <div class="watch-player w-full">
        <Artplayer
          class="art-player"
          @get-instance="getInstance"
          :option="option"
          :style="style"
        />
        <div class="watch-tabs">
          <fwb-tabs v-model="activeTab" variant="underline">
            <fwb-tab name="first" title="简介">
              <div class="watch-video-info pt-4">
                <div
                  class="title-text py-2 text-xl font-bold text-gray-800 dark:text-white"
                >
                  {{ data.title }}
                </div>
                <div class="watch-video-content">
                  <p class="font-normal text-gray-700 dark:text-gray-200">
                    更新时间:{{ COMMON.formatAsDate(data.up_time) }}
                  </p>
                  <p class="font-normal text-gray-700 dark:text-gray-200">
                    评分:{{ data.score }}
                  </p>
                  <p class="font-normal text-gray-700 dark:text-gray-200">
                    年代:{{ data.year }}
                  </p>
                  <p class="font-normal text-gray-700 dark:text-gray-200">
                    导演:{{ data.director }}
                  </p>
                  <p class="font-normal text-gray-700 dark:text-gray-200">
                    演员:{{ data.authors }}
                  </p>
                  <p class="font-normal text-gray-700 dark:text-gray-200">
                    简介:{{ data.content }}
                  </p>
                </div>
              </div>
            </fwb-tab>
            <fwb-tab name="second" title="选集">
              <div
                class="watch-list-content grid grid-cols-6 md:grid-cols-12 pt-3 gap-3"
              >
                <div
                  v-for="(item, index) in data.url_content.split('\n')"
                  :key="index"
                  :class="[index === speedItem ? 'active' : '']"
                  @click="switchRef(index)"
                  class="speed-item rounded text-center text-white bg-gray-300 py-3 text-lg font-semibold leading-none"
                >
                  {{
                    data.video_type == "电影"
                      ? "HD"
                      : index + 1 > 9
                      ? index + 1
                      : "0" + (index + 1)
                  }}
                </div>
              </div>
            </fwb-tab>
          </fwb-tabs>
        </div>
        <div class="watch-like-content">
          <div
            class="content-video-title flex items-center justify-between py-6"
          >
            <div
              class="content-video-title-lift flex items-center justify-between"
            >
              <div class="title-icon">
                <svg
                  class="w-6 h-6 text-gray-800 dark:text-white"
                  aria-hidden="true"
                  xmlns="http://www.w3.org/2000/svg"
                  fill="currentColor"
                  viewBox="0 0 24 24"
                >
                  <path
                    fill-rule="evenodd"
                    d="M5 10.2c.6 0 1 .4 1 1v.2l6 3.5 6-3.5v-.2a1 1 0 1 1 2 0v.8c0 .4-.2.7-.5.9l-7 4a1 1 0 0 1-1 0l-7-4A1 1 0 0 1 4 12v-.8c0-.6.5-1 1-1Zm0 5c.6 0 1 .4 1 1v.2l6 3.4 6-3.4V16a1 1 0 1 1 2 0v.9c0 .3-.2.6-.5.8l-7 4a1 1 0 0 1-1 0l-7-4A1 1 0 0 1 4 17v-1c0-.5.4-1 1-1Z"
                    clip-rule="evenodd"
                  />
                  <path
                    d="M12.5 2.1a1 1 0 0 0-1 0l-7 4a1 1 0 0 0 0 1.8l7 4c.3.2.7.2 1 0l7-4a1 1 0 0 0 0-1.7l-7-4Z"
                  />
                </svg>
              </div>
              <div class="title-text font-bold text-gray-800 dark:text-white">
                推荐
              </div>
            </div>
          </div>
          <div class="content-video-list grid grid-cols-1 md:grid-cols-2 gap-2">
            <div
              v-for="(line, number) in like"
              :key="number"
              class="p-2 h-56 watch-item-card brightness-50 flex gap-2 w-full rounded-sm object-cover object-center"
            >
              <img
                loading="lazy"
                class="video-item-card-image rounded-sm brightness-50"
                :src="line.image"
                alt=""
              />

              <div class="video-rigth">
                <router-link
                  class="video-item-card-title mt-2 text-lg font-bold text-gray-800 hover:text-blue-600 dark:hover:text-blue-600 dark:text-white"
                  :to="'/watch/' + line.ID"
                >
                  {{ line.title }}
                </router-link>
                <div class="watch-video-content">
                  <p class="font-normal text-gray-700 dark:text-gray-200">
                    评分:{{ line.score }}
                  </p>
                  <p class="font-normal text-gray-700 dark:text-gray-200">
                    年代:{{ line.year }}
                  </p>
                  <p class="font-normal text-gray-700 dark:text-gray-200">
                    导演:{{ line.director }}
                  </p>
                  <p
                    class="detail-sketch font-normal text-gray-700 dark:text-gray-200"
                  >
                    简介:{{ line.content }}
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div v-show="data.url_content != null" class="watch-list md:block hidden">
        <div class="content-like-title flex items-center justify-between py-2">
          <div
            class="content-like-title-lift flex items-center justify-between"
          >
            <div
              class="title-text text-lg font-bold text-gray-800 dark:text-white"
            >
              选集
            </div>
          </div>
        </div>
        <div class="watch-list-content grid grid-cols-6 pt-3 gap-3">
          <div
            v-for="(item, index) in data.url_content.split('\n')"
            :key="index"
            :class="[index === speedItem ? 'active' : '']"
            @click="switchRef(index)"
            class="speed-item rounded text-center text-white bg-gray-300 py-3 text-lg font-semibold leading-none"
          >
            {{
              data.video_type == "电影"
                ? "HD"
                : index + 1 > 9
                ? index + 1
                : "0" + (index + 1)
            }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { useToast, FwbTab, FwbTabs } from "flowbite-vue";
import { ref, onMounted, getCurrentInstance } from "vue";
import LoadSkeletonPlay from "../widgets/LoadSkeletonPlay.vue";

import { onBeforeRouteUpdate } from "vue-router";

import Artplayer from "../widgets/ArtplayerWidget.vue";
import Hls from "hls.js";

export default {
  components: {
    LoadSkeletonPlay,
    Artplayer,
    FwbTab,
    FwbTabs,
  },
  setup() {
    const loading = ref(true);
    const toast = useToast();
    const id = ref(null);
    const data = ref(null);
    const like = ref(null);
    const error = ref(null);
    const speedItem = ref(null);
    const activeTab = ref("first");
    var art = null;
    const title = ref(null);

    const { proxy } = getCurrentInstance();
    id.value = proxy.$route.params.id;

    onBeforeRouteUpdate((to, from) => {
      id.value = to.params.id;
      let p = to.query.p;
      if (p == null) {
        speedItem.value = 0;
      } else {
        speedItem.value = parseInt(p) - 1;
      }
      if (to.params.id == from.params.id) {
        let url = ParseUrl();
        art.switchUrl(url, "");
      } else {
        fetchData();
      }
    });

    function fetchData() {
      let api = `${proxy.COMMON.apiUrl}/v1/api/video/jojo/id?id=${id.value}`;
      fetch(api, {
        method: "post",
        headers: {
          "content-type": "application/json",
          Authorization: localStorage.getItem("Authorization"),
        },
      })
        .then((res) => {
          if (!res.ok) {
            const error = new Error(res.statusText);
            error.json = res.json();
            throw error;
          }
          return res.json();
        })
        .then((json) => {
          if (json.code == 200) {
            data.value = json.data;
            like.value = json.like;
            InitItem();
            document.title = data.value.title;
          } else if (json.code == 402) {
            toast.add({
              type: "success",
              time: 3000,
              text: "刷新token成功!",
            });
            localStorage.setItem("Authorization", json.data);
          } else if (json.code == 403) {
            toast.add({
              type: "warning",
              time: 3000,
              text: "登录失效,请重新登录!",
            });
            proxy.COMMON.loginOut();
          } else {
            toast.add({
              type: "success",
              time: 3000,
              text: json.msg,
            });
          }
        })
        .catch((err) => {
          error.value = err;
          if (err.json) {
            return err.json.then((json) => {
              error.value.message = json.message;
              toast.add({
                type: "success",
                time: 3000,
                text: error.value.message,
              });
            });
          }
        })
        .then(() => {
          loading.value = false;
        });
    }

    function InitItem() {
      let p = proxy.$route.query.id;
      if (p == null) {
        speedItem.value = getSpeed(data.value.ID);
      } else {
        speedItem.value = parseInt(p) - 1;
      }
      if (art != null) {
        art.pause();
        InitArt();
      }
    }

    // 获取播放进度时间
    function getPlayTime(keyId) {
      let currentTime = 0;
      let plyr_timesData = localStorage.getItem("plyr_times");
      if (plyr_timesData == "undefined") {
        localStorage.removeItem("plyr_times");
      }
      if (plyr_timesData != undefined) {
        let plyr_times = JSON.parse(plyr_timesData);
        plyr_times.times.forEach((item) => {
          if (item.id === keyId) {
            currentTime = item.time;
          }
        });
      }
      return currentTime;
    }

    // 保存播放进度时间
    function setPlayTime(keyId, currentTime) {
      let plyr_timesData = localStorage.getItem("plyr_times");
      if (plyr_timesData == "undefined") {
        localStorage.removeItem("plyr_times");
      }
      if (plyr_timesData == undefined) {
        var plyrtimes = {
          times: [],
          state: true,
        };
        plyrtimes.times.push({ id: keyId, time: currentTime });
        localStorage.setItem("plyr_times", JSON.stringify(plyrtimes));
      } else {
        let get = false;
        let plyr_times = JSON.parse(plyr_timesData);
        plyr_times.times.forEach((item, i) => {
          if (item.id == keyId) {
            get = true;
            plyr_times.times[i] = { id: keyId, time: currentTime };
          }
        });
        if (!get) {
          plyr_times.times.push({ id: keyId, time: currentTime });
        }
        localStorage.setItem("plyr_times", JSON.stringify(plyr_times));
      }
    }

    // 保存播放进度
    function setSpeed(keyId, itemSpeed) {
      let plyr_speedsData = localStorage.getItem("plyr_speeds");
      if (plyr_speedsData == "undefined") {
        localStorage.removeItem("plyr_speeds");
      }
      if (plyr_speedsData == undefined) {
        var plyrspeeds = {
          speeds: [],
          state: true,
        };
        plyrspeeds.speeds.push({ id: keyId, speed: itemSpeed });
        localStorage.setItem("plyr_speeds", JSON.stringify(plyrspeeds));
      } else {
        let get = false;
        let plyr_speeds = JSON.parse(plyr_speedsData);
        plyr_speeds.speeds.forEach((item, i) => {
          if (item.id == keyId) {
            get = true;
            plyr_speeds.speeds[i] = { id: keyId, speed: itemSpeed };
          }
        });
        if (!get) {
          plyr_speeds.speeds.push({ id: keyId, speed: itemSpeed });
        }
        localStorage.setItem("plyr_speeds", JSON.stringify(plyr_speeds));
      }
    }

    // 获取播放进度
    function getSpeed(keyId) {
      let plyr_speedsData = localStorage.getItem("plyr_speeds");
      if (plyr_speedsData == "undefined") {
        localStorage.removeItem("plyr_speeds");
      }
      let itemSpeed = 0;
      if (plyr_speedsData != undefined) {
        let plyr_speeds = JSON.parse(plyr_speedsData);
        plyr_speeds.speeds.forEach((item) => {
          if (item.id == keyId) {
            itemSpeed = item.speed;
          }
        });
      }
      return itemSpeed;
    }

    function playM3u8(video, url, art) {
      if (Hls.isSupported()) {
        if (art.hls) art.hls.destroy();
        var hlsConfig = {
          xhrSetup: function (xhr, url) {
            xhr.open("get", url, true);
          },
          manifestLoadingMaxRetry: 3,
          manifestLoadingTimeOut: 30000,
        };
        const hls = new Hls(hlsConfig);
        hls.loadSource(url);
        hls.attachMedia(video);
        art.hls = hls;
        art.on("destroy", () => hls.destroy());
      } else if (video.canPlayType("application/vnd.apple.mpegurl")) {
        video.src = url;
      } else {
        art.notice.show = "Unsupported playback format: m3u8";
      }
    }

    function UpdateBackBtn() {
      let speed = speedItem.value + 1;
      title.value = speed > 9 ? "第" + speed + "集" : "第0" + speed + "集";
      if (data.value.video_type === "电影") {
        title.value = data.value.title;
      }
      let backIcon = `<svg t="1684224113388" class="icon back-icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="2449" width="22" height="22"><path d="M395.21518 513.604544l323.135538-312.373427c19.052938-18.416442 19.052938-48.273447 0-66.660212-19.053961-18.416442-49.910737-18.416442-68.964698 0L291.75176 480.290811c-19.052938 18.416442-19.052938 48.273447 0 66.660212l357.633237 345.688183c9.525957 9.207709 22.01234 13.796214 34.497699 13.796214 12.485359 0 24.971741-4.588505 34.466999-13.82896 19.052938-18.416442 19.052938-48.242747 0-66.660212L395.21518 513.604544z" fill="#ffffff" p-id="2450"></path></svg>`;
      let backBtn = {
        name: "backBtn",
        html: `<div class="flex items-center hover:bg-blue-600">${backIcon}<div class="text-lg font-normal">${title.value}</div></div>`,
        tooltip: "",
        style: {
          position: "absolute",
          top: "10px",
          left: "10px",
        },
        click: function () {
          if (art.fullscreen) {
            art.fullscreen = false;
          } else {
            window.history.back();
          }
        },
        mounted: function () {
          // console.info("mounted", args);
        },
      };
      art.layers.update(backBtn);
      setSpeed(data.value.ID, speedItem.value);
      UpdateHelpBtn();
    }

    function UpdateHelpBtn() {
      let helpIcon = `<svg class="w-7 h-7 text-white" aria-hidden="true" xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" viewBox="0 0 24 24"><path fill-rule="evenodd" d="M2 12C2 6.477 6.477 2 12 2s10 4.477 10 10-4.477 10-10 10S2 17.523 2 12Zm11-4a1 1 0 1 0-2 0v5a1 1 0 1 0 2 0V8Zm-1 7a1 1 0 1 0 0 2h.01a1 1 0 1 0 0-2H12Z" clip-rule="evenodd"/></svg>`;
      let helpBtn = {
        name: "helpBtn",
        html: `<div class="backHelp-content">${helpIcon}</div>`,
        tooltip: "",
        style: {
          position: "absolute",
          top: "10px",
          right: "15px",
        },
        click: function () {
          window.open(proxy.COMMON.helpUrl, "_blank");
        },
        mounted: function () {
          // console.info("mounted", args);
        },
      };
      art.layers.update(helpBtn);
    }

    function ParseUrl() {
      let urlTextList = data.value.url_content.split("\n");
      for (let i = 0; i < urlTextList.length; i++) {
        if (speedItem.value === i) {
          let urlLineList = urlTextList[i].split("$");
          if (urlLineList.length == 2) {
            return proxy.COMMON.chunkUrl(urlLineList[1]);
          }
        }
      }
      return "";
    }

    function initArtTv() {
      if (data.value.video_type === "电影") {
        if (art.controls.nextBtn != undefined) {
          art.controls.remove("nextBtn");
        }
        return;
      }
      let nextIcon = `<?xml version="1.0" standalone="no"?><!DOCTYPE svg PUBLIC "-//W3C//DTD SVG 1.1//EN" "http://www.w3.org/Graphics/SVG/1.1/DTD/svg11.dtd"><svg t="1659155731129" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="1725" xmlns:xlink="http://www.w3.org/1999/xlink" width="28" height="28"><defs><style type="text/css"></style></defs><path d="M721.454545 551.563636L302.545455 795.927273c-30.254545 18.618182-69.818182-4.654545-69.818182-39.563637v-488.727272c0-34.909091 39.563636-58.181818 69.818182-39.563637l418.90909 244.363637c30.254545 16.290909 30.254545 62.836364 0 79.127272z" p-id="1726" fill="#ffffff"></path><path d="M791.272727 684.218182c0 32.581818-30.254545 60.509091-69.818182 60.509091s-69.818182-27.927273-69.818181-60.509091V339.781818c0-32.581818 30.254545-60.509091 69.818181-60.509091s69.818182 27.927273 69.818182 60.509091v344.436364z" p-id="1727" fill="#ffffff"></path></svg>`;
      let next = {
        name: "nextBtn",
        position: "left",
        index: 11,
        html: `<div class="next-btn">${nextIcon}</div>`,
        tooltip: "下一集",
        style: {
          color: "green",
        },
        click: function () {
          speedItem.value++;
          art.switchUrl(ParseUrl(), "");
          UpdateBackBtn();
        },
      };
      art.controls.update(next);
    }

    function switchUrl(dataSpeed) {
      speedItem.value = dataSpeed;
      art.switchUrl(ParseUrl(), "");
      UpdateBackBtn();
    }

    function InitArt() {
      art.url = ParseUrl();
      art.on("ready", () => {
        art.currentTime = getPlayTime(data.value.ID + "$" + speedItem.value);
        art
          .play()
          .then(() => {})
          .catch((err) => {
            console.log(err);
          })
          .finally(() => {});
        art.on("video:timeupdate", () => {
          setPlayTime(data.value.ID + "$" + speedItem.value, art.currentTime);
        });
      });

      art.on("control", (state) => {
        if (state) {
          if (!art.isLock) {
            art.layers.backBtn.style.display = "";
            art.layers.helpBtn.style.display = "";
          }
        } else {
          art.layers.backBtn.style.display = "none";
          art.layers.helpBtn.style.display = "none";
        }
      });
      art.on("lock", (state) => {
        if (state) {
          art.layers.backBtn.style.display = "none";
          art.layers.helpBtn.style.display = "none";
          art.controls.progress.style.display = "none";
        } else {
          art.layers.backBtn.style.display = "";
          art.layers.helpBtn.style.display = "";
          art.controls.progress.style.display = "";
        }
      });
      art.on("error", (error, reconnectTime) => {
        console.info(error, reconnectTime);
      });
      art.on("dblclick", () => {
        art
          .play()
          .then(() => {})
          .catch((err) => {
            console.log(err);
          })
          .finally(() => {});
      });
      UpdateBackBtn();
      initArtTv();
      setTimeout(() => {
        art
          .play()
          .then(() => {})
          .catch((err) => {
            console.log(err);
          })
          .finally(() => {});
      }, 2000);
    }

    const artInstance = async (data) => {
      art = data;
      window.art = art;
      InitArt();
    };

    const switchRef = async (data) => {
      switchUrl(data);
    };

    onMounted(() => {
      fetchData();
    });

    return {
      data,
      speedItem,
      like,
      loading,
      toast,
      activeTab,
      artInstance,
      switchRef,
      option: {
        url: "",
        theme: "#5684f6",
        autoSize: false,
        miniProgressBar: false,
        playsInline: false,
        autoMini: true,
        fullscreen: true,
        fullscreenWeb: true,
        autoOrientation: true,
        flip: true,
        lock: true,
        hotkey: true,
        fastForward: true,
        playbackRate: true,
        aspectRatio: true,
        setting: true,
        customType: {
          m3u8: playM3u8,
        },
        layers: [],
        icons: {
          state:
            '<svg xmlns="http://www.w3.org/2000/svg" width="70" height="70" viewBox="0 0 400 400"><path fill="#69f" d="M199.6,19.64a180,180,0,1,1-180,180A180,180,0,0,1,199.6,19.64Z"/><path fill="#fff" d="M295.987,191.5a10,10,0,0,1,0,17.556L153.531,286.767a10,10,0,0,1-14.788-8.781l0.027-155.464a10,10,0,0,1,14.792-8.775Z"/></svg>',
        },
      },
      style: {
        width: "100%",
      },
    };
  },
  methods: {
    getInstance(art) {
      this.artInstance(art);
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>
